	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"http://onethingwell.org/post/898477013/gobolinux\">onethingwell</a>:</p>\n<blockquote>\n<blockquote>\n<p>GoboLinux is a modular Linux distribution: it organizes the programs in your system in a new, logical way. Instead of\nhaving parts of a program thrown at <code>/usr/bin</code>, other parts at <code>/etc</code> and yet more parts thrown\nat <code>/usr/share/something/or/another</code>, <em>each program gets its own directory tree</em>, keeping them all neatly separated\nand allowing you to see everything that’s installed in the system and which files belong to which programs in a\nsimple and obvious way.</p>\n</blockquote>\n<p>Off-topic, I know, but this strikes me as an eminently sensible approach.</p>\n</blockquote>\n<p>Sounds like what OS X does with applications. Very sensible.</p>","fields":{"post":{"title":"GoboLinux","link":"http://www.gobolinux.org/","timestamp":"2010-08-03T15:19Z","date":"August 3, 2010 3:19pm UTC"}}}},"pathContext":{"slug":"/gobolinux"}}

